* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", sans-serif;
  /* background: #18191a; */
}
.messageSender {
  display: flex;
  margin: 100px auto;
  flex-direction: column;
  background-color: #242526;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px gray;
  width: 80%;
}
/* messageSender__Bottom */

.messageSender_top {
  display: flex;
  border-bottom: 1px solid #4e4f50;
  padding: 20px;
}
.messageSender_top > form {
  flex: 1;
  display: flex;
}
.messageSender_top > form > input {
  outline: none;
  outline-width: 0;
  border: none;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #4e4f50;
  width: 30%;
  font-size: 18px;
  color: #a5a8ac;
  text-transform: capitalize;
}
.messageSender_top > form > button {
  display: none;
}
.messageSender_input {
  flex: 1;
}
input::placeholder {
  font-size: 18px;
  color: #a5a8ac;
}

/* messageSender__Bottom */

.messageSender_bottom {
  display: flex;
  justify-content: space-evenly;
}
.messageSender_option {
  display: flex;
  padding: 20px;
  align-items: center;
  margin: 6px;
  color: #a5a8ac;
}
.messageSender_option > h3 {
  font-size: medium;
  margin-left: 10px;
}
.messageSender_option:hover {
  background-color: #4e4f50;
  border-radius: 20px;
}
